html,
body,
#root {
  height: 100%;
}
@font-face {
  font-family: "Alphin-Merytous-St";
  src: url("./fonts/Alphin-Merytous-St.ttf.woff") format("woff"),
    url("./fonts/Alphin-Merytous-St.ttf.svg#Alphin-Merytous-St") format("svg"),
    url("./fonts/Alphin-Merytous-St.ttf.eot"),
    url("./fonts/Alphin-Merytous-St.ttf.eot?#iefix") format("embedded-opentype");
  font-weight: normal;
  font-style: normal;
}
* {
  color: #a3a3a3;
  font-weight: 600;
}
#root {
  display: flex;
  align-items: center;
}
body {
  background-image: radial-gradient(
    circle at top right,
    #6633ce 50%,
    #f8f8fa 50%
  ) !important;
  background-size: 400% 400% !important;
  background-position: left bottom !important;
}
.mode-transition-animation,
.mode-transition-animation .switch-mode,
.mode-transition-animation .form,
.mode-transition-animation .form-title h1,
.mode-transition-animation .form-body h1,
.mode-transition-animation .form-control,
.mode-transition-animation button,
.mode-transition-animation .form .form-footer,
.mode-transition-animation .form .form-footer span,
.mode-transition-animation .form .form-footer i {
  transition: background-position 1s linear, backgorund-color 1s ease,
    border-radius 1s ease, border 1s ease, color 1s ease !important;
}
.bg-dark {
  background-position: right top !important;
}
.switch-mode {
  position: absolute;
  top: 0;
  right: 0;
  width: 50px;
  height: 50px;
}
.ux-col,
.ui-col {
  user-select: none; /* supported by Chrome and Opera */
  -webkit-user-select: none; /* Safari */
  -khtml-user-select: none; /* Konqueror HTML */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none;
  max-width: 135px;
}
.ux-col span {
  color: #6633ce;
}
.ui-col span {
  color: #f8f8fa;
}
.ux-col .desc,
.ui-col .desc {
  font-size: 20px;
  max-width: 50px;
}
.form {
  width: 100%;
  max-width: 330px;
  margin: auto;
  border: 1.5px solid #c7c7c7;
  background-image: radial-gradient(
    circle at top right,
    #312e37 50%,
    #f8f8fa 50%
  ) !important;
  background-size: 400% 400% !important;
  background-position: left bottom !important;
}
body.bg-dark .form {
  border-radius: 15px;
  border: 1.5px solid #312e37;
  background-position: right top !important;
}
.form-title h1 {
  font-family: "Alphin-Merytous-St" !important;
  border-top: 2px solid #c7c7c7;
  border-bottom: 2px solid #c7c7c7;
  color: #7b7b7b;
}
body.bg-dark .form-title h1 {
  color: #f8f8fa;
  border-color: #ff9000;
}
body.bg-dark .form-body h1 {
  color: #f8f8fa;
}
.form-title h1::before,
.form-title h1::after {
  border-radius: 50%;
  background-color: #a1a1a1;
  content: "";
  height: 7px;
  width: 7px;
  margin-left: 0.3rem;
  margin-right: 0.4rem;
}
.form .form-body {
  height: 80%;
  max-height: 500px;
}
.form-control {
  border: 1.5px solid #c7c7c7;
  border-radius: 0;
  color: #a3a3a3;
  height: calc(26px + 22.4px);
  background-clip: unset;
  background-image: radial-gradient(
    circle at top right,
    #23222a 50%,
    #f8f8fa 50%
  ) !important;
  background-size: 400% 400% !important;
  background-position: left bottom !important;
}
body.bg-dark .form-control {
  border-radius: 10px;
  border-color: #23222a;
  background-position: right top !important;
}
input:active,
input:focus,
input:hover,
button:active,
button:focus {
  box-shadow: none !important;
  outline: none !important;
  border-color: #7b7b7b !important;
}
body.bg-dark input:active,
body.bg-dark input:focus,
body.bg-dark input:hover,
body.bg-dark button:active,
body.bg-dark button:focus,
body.bg-dark button:hover {
  color: #f8f8fa !important;
  border-color: #ff9000 !important;
}

input::placeholder {
  color: #c7c7c7 !important;
}
button {
  font-size: 15px !important;
  color: #f8f8fa !important;
  padding-top: 14px !important;
  padding-bottom: 14px !important;
  background-image: radial-gradient(
    circle at top right,
    #ff9000 50%,
    #7b7b7b 50%
  ) !important;
  background-size: 400% 400% !important;
  background-position: left bottom !important;
  border: none !important;
  border-radius: 0 !important;
}
body.bg-dark button {
  background-position: right top !important;
  color: #23222a !important;
  border-radius: 10px !important;
}
span.forgot-pass,
.form-footer span {
  cursor: pointer;
}
.form .form-footer {
  border-top: 1.5px solid #c7c7c7;
}
body.bg-dark .form .form-footer {
  border-top: 1.5px solid #23222a;
}
body.bg-dark .form .form-footer span,
body.bg-dark .form .form-footer i {
  color: #ff9000 !important;
}
